.home-page {
    
    .img-background {
        width: 100%;
        height: 100vh;
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        background:linear-gradient(rgba(30,32,34,.4),rgba(15,18,21,.4)),url(../assets/Home-dark.jpg) center;
        background-size: cover;
    }

    .info {
        margin-top: 10%;
        text-align: center;
        color: white;

        p {
            margin: 5rem 0 0.5rem 0;
        }

        .button {
            text-decoration: none;
            padding: 1rem;
            margin: 1rem;
            background: red;
            color: white;
            font-weight: 900;
            border: none;
            border-radius: 2rem;
        }
    }
}